body {
  font-family: "Open Sans", sans-serif;
  background-color: #0e0e0e;
  color: white;
}

h5 {
  font-weight: 800;
  font-size: 1.5em;
}

button {
  margin-top: 10px;
}

#content {
  background-color: black;
  margin: auto;
  height: 100vh;
}

#user-interface {
  padding-bottom: 10px;
  max-width: 600px;
  margin: auto;
}

.input-container {
  display: flex;
  justify-content: center;
}

article {
  background-color: white;
  font-weight: bold;
  box-shadow: 0 1px 8px 1px rgba(61, 61, 61, 0.479);
  display: flex;
  flex-direction: column;
}

.title {
  padding: 0.8em 1.5em 0.8em 1.5em;
  text-shadow: 0.5px 0.5px 0.2px #00000085;
}

a {
  color: #575757;
  transition: all 0.25s;
}

a.highlighted {
  color: #f32373;
  text-decoration: none;
}

img {
  width: 100%;
  height: 300px;
  object-fit: cover;
}

#image-grid-area {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 2rem;
  padding: 2rem;
}

/* media queries */
@media screen and (max-width: 400px) {
  h5 {
    font-size: 1em;
  }
}
@media screen and (max-width: 230px) {
  h5 {
    font-size: 0.8em;
  }

  .title {
    padding: 0.8em;
  }
}

